<template>
  <div 
    class="floating-buttons-cloud" 
    :style="{ backgroundImage: `url(${logo})` }"
    ref="cloudContainer"
    @touchstart="handleTouchStart"
    @touchmove="handleTouchMove"
    @touchend="handleTouchEnd"
  >
    <div 
      class="button-item do-invert" 
      v-for="(button, index) in buttons" 
      :key="`btn-${index}`"
      :style="getButtonStyle(index)"
      @mouseenter="handleHover(index, true)"
      @mouseleave="handleHover(index, false)"
    >
      <router-link :to="button.to" class="cloud-button">
        <i :class="button.icon"></i> {{ button.text }}
      </router-link>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, onUnmounted } from 'vue'
import logo from '@/assets/home-hero-cat.png'

const cloudContainer = ref(null)
let animationPaused = false
let currentTime = ref(0)
let animationId = null
let hoveredIndex = ref(-1)

// Touch управление для мобильных устройств
let touchStartX = 0
let touchStartY = 0
let touchStartTime = 0
let manualOffset = ref(0) // Смещение от touch жестов
let isMobile = ref(false)

const buttons = [
{ to: '/ai-agents', icon: 'fas fa-robot', text: 'AI Agents' },
  { to: '/nft', icon: 'fas fa-image', text: 'NFT Space' },
  { to: '/tokens', icon: 'fas fa-rocket', text: 'Token Launchpad' },
  { to: '/cashback', icon: 'fas fa-gift', text: 'SOL Cashback' },
  { to: '/dao', icon: 'fas fa-vote-yea', text: 'DAO Voting' },
  { to: '/games', icon: 'fas fa-gamepad', text: 'Games Arena' },
  { to: '/my/referrals', icon: 'fas fa-user', text: 'Referrals' },
  { to: '/rewards', icon: 'fas fa-trophy', text: 'Rewards' },
  { to: '/welcome', icon: 'fas fa-plus', text: 'More...' }
]

const handleHover = (index, isHovering) => {
  hoveredIndex.value = isHovering ? index : -1
  
  // Останавливаем анимацию только при наведении на кнопку
  if (isHovering) {
    animationPaused = true
    if (animationId) {
      cancelAnimationFrame(animationId)
    }
  } else {
    animationPaused = false
    animate()
  }
}

const animate = () => {
  if (animationPaused) return
  
  // 🎛️ СКОРОСТЬ АНИМАЦИИ - чем больше значение, тем быстрее движение
  // Диапазон: 0.001 (очень медленно) - 0.02 (быстро)
  currentTime.value += 0.0035
  animationId = requestAnimationFrame(animate)
}

const getButtonStyle = (index) => {
  // Центр облака (динамически на основе размеров контейнера)
  const containerRect = cloudContainer.value?.getBoundingClientRect()
  const centerX = containerRect ? containerRect.width / 2 : 280 // Половина ширины контейнера
  const centerY = containerRect ? containerRect.height / 2 : 100 // Половина высоты контейнера
  
  // 🎛️ РАЗМЕРЫ ЭЛЛИПСА - настройка формы орбиты
  const radiusX = containerRect ? Math.min(containerRect.width, containerRect.height) * 0.6 : 150 // Горизонтальный радиус (ширина орбиты) 0.1-0.5
  const radiusY = containerRect ? Math.min(containerRect.width, containerRect.height) * 0.3 : 60 // Вертикальный радиус (высота орбиты) 0.05-0.3
  
  const angle = (index / buttons.length) * Math.PI * 2 + currentTime.value + manualOffset.value
  const x = centerX + Math.cos(angle) * radiusX
  const y = centerY + Math.sin(angle) * radiusY
  
  // 🎛️ ПЕРСПЕКТИВА - настоящий эффект глубины (только размер)
  // Math.sin(angle): -1 (верх эллипса, сзади) до +1 (низ эллипса, спереди)
  const perspectiveFactor = Math.sin(angle) // От -1 (сзади) до +1 (спереди)
  const minScale = 0.4 // 🎛️ Размер элементов сзади: 0.1-0.6
  const maxScale = 1.0 // 🎛️ Размер элементов спереди: 0.8-1.5
  const scale3D = minScale + (perspectiveFactor + 1) * (maxScale - minScale) / 2
  
  // 🎛️ HOVER ЭФФЕКТ - все элементы при hover становятся размером 1.1
  const hoverScale = 1.1 // 🎛️ Абсолютный размер при hover 0.8-1.5
  const finalScale = hoveredIndex.value === index ? hoverScale : scale3D
  
  return {
    left: `${x}px`,
    top: `${y}px`,
    transform: `translate(-50%, -50%) scale(${finalScale})`
  }
}

// Определение мобильного устройства
const checkMobile = () => {
  isMobile.value = window.innerWidth <= 768
}

// Touch события для мобильного управления
const handleTouchStart = (e) => {
  touchStartX = e.touches[0].clientX
  touchStartY = e.touches[0].clientY
  touchStartTime = Date.now()
}

const handleTouchMove = (e) => {
  e.preventDefault() // Предотвращаем скролл страницы
}

// 🎛️ TOUCH НАСТРОЙКИ для мобильных жестов
const handleTouchEnd = (e) => {
  const touchEndX = e.changedTouches[0].clientX
  const touchEndY = e.changedTouches[0].clientY
  const touchDuration = Date.now() - touchStartTime
  
  const deltaX = touchEndX - touchStartX
  const deltaY = touchEndY - touchStartY
  const distance = Math.sqrt(deltaX * deltaX + deltaY * deltaY)
  
  // 🎛️ ПАРАМЕТРЫ СВАЙПА
  const minSwipeDistance = 50 // 🎛️ Минимальная дистанция свайпа (px) 30-100
  const maxSwipeDuration = 300 // 🎛️ Максимальное время свайпа (ms) 200-500
  const swipeStrength = 0.5 // 🎛️ Сила влияния свайпа на движение 0.1-1.0
  
  // Если это свайп (быстрое движение на достаточное расстояние)
  if (distance > minSwipeDistance && touchDuration < maxSwipeDuration) {
    const swipeDirection = Math.atan2(deltaY, deltaX)
    // Добавляем смещение в направлении свайпа
    manualOffset.value += swipeDirection * swipeStrength
  }
}

onMounted(() => {
  checkMobile()
  window.addEventListener('resize', checkMobile)
  animate()
})

onUnmounted(() => {
  if (animationId) {
    cancelAnimationFrame(animationId)
  }
  window.removeEventListener('resize', checkMobile)
})
</script>

<style scoped>
.floating-buttons-cloud {
  position: relative;
  width: 100%;
  max-width: 560px;
  height: 300px;
  margin: 0 auto;
  /* overflow: hidden; */
  background-color: transparent;
  /* background-image: url('https://bitcat.fun/data/logo.png'); */
  background-size: 300px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

.button-item {
  position: absolute;
  transition: transform 0.3s ease;
  will-change: transform;
}

.button-item:hover {
  z-index: 100 !important;
}

.cloud-button {
  padding: 12px 20px;
  border-radius: 30px;
  font-weight: 600;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  font-size: 1.1rem;
  background: linear-gradient(135deg, #ff9800 0%, #ff5722 100%);
  color: white;
  cursor: pointer;
  white-space: nowrap;
  transition: all 0.3s ease;
}

.button-item:hover .cloud-button {
  box-shadow: 0 8px 30px rgba(255, 152, 0, 0.6);
}

@media (max-width: 768px) {
  .floating-buttons-cloud {
    width: 75%;
    touch-action: none; /* Предотвращаем стандартные touch жесты */
    background-size: 250px;
  }
  
  .cloud-button {
    padding: 6px 12px;
    gap: 4px;
  }
}
</style> 