<script setup>
import { ref, onMounted } from 'vue'
import axios from 'axios'
import heroImage from '@/assets/bitcat-tweet.png'
import SocialLinksSection from '@/components/pages/home/SocialLinksSection.vue'
import FloatingButtonsCloud from '@/components/pages/home/FloatingButtonsCloud.vue'

const contractAddress = '4j9bDg7iWNah1Qa61rrqwWZMtEdqV3fV56SzyhfNpump'
const showCopyTooltip = ref(false)
const contractInput = ref(null)
const twitterEmbed = ref(null)
const burnedAmount = ref(0)

const formatBurnedAmount = (amount) => {
  if (amount >= 1000000) {
    return (amount / 1000000).toFixed(1) + 'M'
  } else if (amount >= 1000) {
    return (amount / 1000).toFixed(0) + 'K'
  }
  return amount.toString()
}

const fetchBurnStats = async () => {
  try {
    const response = await axios.get('/token-burn/stats')
    if (response.data.success && response.data.stats) {
      burnedAmount.value = response.data.stats.totalBurnedTokens
    }
  } catch (error) {
    console.error('Failed to fetch burn stats:', error)
  }
}

const loadTwitterWidget = () => {
  // Удаляем существующий скрипт, если он есть
  const existingScript = document.querySelector('script[src="https://platform.twitter.com/widgets.js"]')
  if (existingScript) {
    existingScript.remove()
  }
  
  // Создаем новый скрипт
  const script = document.createElement('script')
  script.setAttribute('src', 'https://platform.twitter.com/widgets.js')
  script.setAttribute('async', true)
  script.setAttribute('charset', 'utf-8')
  document.head.appendChild(script)
}

onMounted(() => {
  loadTwitterWidget()
  fetchBurnStats()
})

const copyToClipboard = async () => {
  try {
    await navigator.clipboard.writeText(contractAddress)
    showCopyTooltip.value = true
    contractInput.value?.select()
    
    setTimeout(() => {
      showCopyTooltip.value = false
    }, 2000)
  } catch (err) {
    console.error('Failed to copy text: ', err)
  }
}
</script>

