<template>
  <div class="home-page">
    <HeroSection />
    <ListedOnSection />
    
    <template v-if="isClient">
      <div v-show="true" class="section-wrapper token-launch-section" :class="{ 'fade-in': visibleSections.tokenLaunch }">
        <TokenLaunchpadSection v-if="loadedSections.tokenLaunch" />
      </div>

      <div v-show="true" class="section-wrapper features-section" :class="{ 'fade-in': visibleSections.features }">
        <FeaturesSection v-if="loadedSections.features" />
      </div>

      <!-- <div v-show="true" class="section-wrapper features-section" :class="{ 'fade-in': true }">
        <LightFeaturesSection />
      </div> -->
      
      <div v-show="true" class="section-wrapper crowdfunding-section" :class="{ 'fade-in': visibleSections.crowdfunding }">
        <CrowdfundingSection v-if="loadedSections.crowdfunding" />
      </div>
      
      <div v-show="true" class="section-wrapper bitcat-history-section" :class="{ 'fade-in': visibleSections.bitcotHistory }">
        <BitcatHistorySection v-if="loadedSections.bitcotHistory" />
      </div>
      
      <div v-show="true" class="section-wrapper how-to-buy-section" :class="{ 'fade-in': visibleSections.howToBuy }">
        <HowToBuySection v-if="loadedSections.howToBuy" />
      </div>
      
      <div v-show="true" class="section-wrapper bitcotnomics-section" :class="{ 'fade-in': visibleSections.bitcotnomics }">
        <BitcatnomicsSection v-if="loadedSections.bitcotnomics" />
      </div>
      
      <div v-show="true" class="section-wrapper community-section" :class="{ 'fade-in': visibleSections.community }">
        <CommunitySection v-if="loadedSections.community" />
      </div>
      
      <div v-show="true" class="section-wrapper contact-section" :class="{ 'fade-in': visibleSections.contact }">
        <ContactSection v-if="loadedSections.contact" />
      </div>
    </template>
  </div>
</template>

<script setup>
import { defineAsyncComponent, onMounted, onUnmounted, ref, nextTick } from 'vue'
import LightFeaturesSection from '@/components/pages/home/LightFeaturesSection.vue'

// Only HeroSection is loaded immediately since it's visible on initial load
import HeroSection from '@/components/pages/home/HeroSection.vue'

// Detect if we're on client-side to avoid SSR issues
const isClient = ref(false)

// Track which sections have been loaded
const loadedSections = ref({
  tokenLaunch: false,
  listedOn: false,
  features: false,
  crowdfunding: false,
  bitcotHistory: false,
  howToBuy: false,
  bitcotnomics: false,
  community: false,
  contact: false
})

const TokenLaunchpadSection = defineAsyncComponent({
  loader: () => import('@/components/pages/home/TokenLaunchpadSection.vue'),
  delay: 0,
  timeout: 30000
})

// Lazy load all other components with loading indicator and error handling
const CrowdfundingSection = defineAsyncComponent({
  loader: () => import('@/components/pages/home/CrowdfundingSection.vue'),
  delay: 0,
  timeout: 30000
})
const ListedOnSection = defineAsyncComponent({
  loader: () => import('@/components/pages/home/ListedOnSection.vue'),
  delay: 0,
  timeout: 30000
})
const FeaturesSection = defineAsyncComponent({
  loader: () => import('@/components/pages/home/FeaturesSection.vue'),
  delay: 0,
  timeout: 30000
})
const BitcatHistorySection = defineAsyncComponent({
  loader: () => import('@/components/pages/home/BitcatHistorySection.vue'),
  delay: 0,
  timeout: 30000
})
const HowToBuySection = defineAsyncComponent({
  loader: () => import('@/components/pages/home/HowToBuySection.vue'),
  delay: 0,
  timeout: 30000
})
const BitcatnomicsSection = defineAsyncComponent({
  loader: () => import('@/components/pages/home/BitcatnomicsSection.vue'),
  delay: 0,
  timeout: 30000
})
const CommunitySection = defineAsyncComponent({
  loader: () => import('@/components/pages/home/CommunitySection.vue'),
  delay: 0,
  timeout: 30000
})
const ContactSection = defineAsyncComponent({
  loader: () => import('@/components/pages/home/ContactSection.vue'),
  delay: 0,
  timeout: 30000
})

// Visibility tracking for sections
const visibleSections = ref({
  tokenLaunch: false,
  listedOn: false,
  features: false,
  crowdfunding: false,
  bitcotHistory: false,
  howToBuy: false,
  bitcotnomics: false,
  community: false,
  contact: false
})

// Use a more efficient approach with a single IntersectionObserver
let observer = null

// Function to load a section when it's about to be visible
const loadSection = (sectionId) => {
  if (!loadedSections.value[sectionId]) {
    // Use setTimeout to stagger the loading and avoid blocking the main thread
    setTimeout(() => {
      loadedSections.value[sectionId] = true
    }, 100) // Small delay to prevent UI blocking
  }
}

// Set up a single observer for all sections
const setupObserver = () => {
  if (typeof IntersectionObserver === 'undefined') {
    // Fallback for browsers that don't support IntersectionObserver
    Object.keys(loadedSections.value).forEach(key => {
      loadedSections.value[key] = true
    })
    return
  }
  
  observer = new IntersectionObserver((entries) => {
    entries.forEach(entry => {
      // Get section ID from class name
      const sectionClass = Array.from(entry.target.classList)
        .find(cls => cls.endsWith('-section') && cls !== 'section-wrapper')
      
      if (!sectionClass) return
      
      const sectionId = sectionClass
        .replace('token-launch-section', 'tokenLaunch')
        .replace('listed-on-section', 'listedOn')
        .replace('features-section', 'features')
        .replace('crowdfunding-section', 'crowdfunding')
        .replace('bitcat-history-section', 'bitcotHistory')
        .replace('how-to-buy-section', 'howToBuy')
        .replace('bitcotnomics-section', 'bitcotnomics')
        .replace('community-section', 'community')
        .replace('contact-section', 'contact')
      
      if (entry.isIntersecting) {
        // Mark section as visible
        visibleSections.value[sectionId] = true
        // Load the section content
        loadSection(sectionId)
      }
    })
  }, { 
    rootMargin: '200px 0px', // Load sections before they come into view
    threshold: 0.01 // Trigger when just 1% of the element is visible
  })
}

// Load sections in sequence with delays to prevent UI blocking
const loadSectionsSequentially = () => {
  // Load the first few sections immediately
  loadSection('tokenLaunch')
  loadSection('listedOn')
  
  // Load remaining sections with increasing delays
  setTimeout(() => loadSection('features'), 300)
  setTimeout(() => loadSection('crowdfunding'), 600)
  setTimeout(() => loadSection('bitcotHistory'), 900)
  setTimeout(() => loadSection('howToBuy'), 1200)
  setTimeout(() => loadSection('bitcotnomics'), 1500)
  setTimeout(() => loadSection('community'), 1800)
  setTimeout(() => loadSection('contact'), 2100)
}

onMounted(() => {
  // Mark as client-side rendered
  isClient.value = true
  
  // Set up observer after a small delay to ensure DOM is ready
  nextTick(() => {
    setupObserver()
    
    // Observe all section wrappers
    document.querySelectorAll('.section-wrapper').forEach(section => {
      if (observer) observer.observe(section)
    })
    
    // Start loading sections sequentially after a short delay
    setTimeout(loadSectionsSequentially, 500)
  })
})

onUnmounted(() => {
  // Clean up observer when component is unmounted
  if (observer) {
    observer.disconnect()
    observer = null
  }
})
</script>

<style scoped>
.home-page {
  padding: 0;
  contain: content; /* Improve performance with CSS containment */
}

.section-wrapper {
  opacity: 0;
  transform: translateY(20px);
  transition: opacity 0.8s ease, transform 0.8s ease;
  will-change: opacity, transform; /* Hint to browser for optimization */
  contain: content; /* Improve performance with CSS containment */
}

.section-wrapper.fade-in {
  opacity: 1;
  transform: translateY(0);
}

/* Add height placeholders to prevent layout shifts */
.token-launch-section {
  min-height: 200px;
}

.listed-on-section {
  min-height: 200px;
}

.features-section {
  min-height: 400px;
}

.crowdfunding-section {
  min-height: 400px;
}

.bitcat-history-section {
  min-height: 400px;
}

.how-to-buy-section {
  min-height: 400px;
}

.bitcotnomics-section {
  min-height: 400px;
}

.community-section {
  min-height: 300px;
}

.contact-section {
  min-height: 100px;
}
</style>